.topBox {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box; 
    border: 4px solid rgba(205, 68, 30, 1);
    border-radius: 45px 45px 45px 45px;
    width: 80%;
    height: 60%;
    left: 10%;
    top: 20%;
    text-align: center;
    margin: auto;
}
#topName{
    position: absolute;
    font-size: 24px;
    text-align: center;
    margin-top: -90%;
}
.player_1 {
    position: absolute;
    margin-top: -50%;
    margin-left: 0%;
    border: 4px solid rgba(205, 68, 30, 1);
    border-radius: 20px;
    width: 100%;
    height: 9%;
    border: 1px 0px 0px 0px;
    opacity: 0px;
}
#playerNumber {
    position: relative;
    margin-top: -1%;
    margin-left: -1%;
    border: 4px solid rgba(205, 68, 30, 1);
    background: rgba(190, 70, 33, 1);
    border-radius: 20px;
    width: 20%;
    border: 1px 0px 0px 0px;
    opacity: 0px;
    color: rgba(255, 255, 255, 1);
}
#playerWalletNumber {
    position: relative;
    margin-top: -11%;
}
#playerBalance {
    position: relative;
    margin-left: 78.5%;
    margin-top: -11%;
    border: 4px solid rgba(205, 68, 30, 1);
    background: rgba(190, 70, 33, 1);
    border-radius: 20px;
    width: 20%;
    border: 1px 0px 0px 0px;
    opacity: 0px;
    color: rgba(255, 255, 255, 1);
}
.player_2 {
    position: absolute;
    margin-top: -20%;
    margin-left: 0%;
    border: 4px solid rgba(205, 68, 30, 1);
    border-radius: 20px;
    width: 100%;
    height: 9%;
    border: 1px 0px 0px 0px;
    opacity: 0px;
}
